@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);

*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
:root{
  --btn-default-bg :#E0E0E0;
  --btn-default-bg-hover : #AEAEAE;
  --btn-default-color :#3F3F3F;
  --btn-default-shadow : 0px 2px 3px rgba(51, 51, 51, 0.2);

  --btn-disabled-color : #9E9E9E;

  --btn-outline-bg : transparent;
  --btn-outline-color : #3D5AFE;
  --btn-outline-bg-hover :rgba(41, 98, 255, 0.1);
  --btn-outline-shadow : none;

  --btn-primary-color : #FFFFFF;
  --btn-primary-bg : #2962FF;
  --btn-primary-shdow : 0px 2px 3px rgba(41, 98, 255, 0.2);
  --btn-primary-bg-hover : #0039CB;

  --btn-secondary-bg :#455A64;
  --btn-secondary-shadow :  0px 2px 3px rgba(69, 90, 100, 0.2);
  --btn-secondary-color : #FFFFFF;
  --btn-secondary-bg-hover : #1C313A;

  --btn-danger-bg :#D32F2F;
  --btn-danger-shadow :  0px 2px 3px rgba(211, 47, 47, 0.2);
  --btn-danger-color : #FFFFFF;
  --btn-danger-bg-hover : #9A0007;

}

body{
  font-family: 'Fira Mono', monospace;
  min-height: 100vh;
  overflow-x: hidden;
  padding : 2rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(41, 41, 41);

}

#root{
  width: 100%;

}
button{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: .5rem;
    gap: .5rem;
    padding : .7rem 1rem;
    border: none;
    outline: none;
    background-color: transparent;
    color: inherit;
    font-family: inherit;
    border-radius: 6px;
    transition: all .4s ease;
    cursor: pointer;

    
}
button span{
    display: block;
}
button img{
    width: 1rem;
    margin-top: .2rem;
}
.default{
    color: var(--btn-default-color);
    background: var(--btn-default-bg);
    box-shadow: var(--btn-default-shadow);
    background-color: var(--btn-default-bg);
   
}
.default:hover,
.default:focus{
    background-color: var(--btn-default-bg-hover);
}
.default.disabled{
    color: var(--btn-disabled-color);
    background-color: var(--btn-default-bg);
    box-shadow: none;
    cursor: unset;
}
.primary{
    color: var(--btn-primary-color);
    background: var(--btn-primary-bg);
    box-shadow: var(--btn-primary-shadow);
}
.primary:hover{
    background: var(--btn-primary-bg-hover);
}

.secondary{
    background: var(--btn-secondary-bg);
    box-shadow: var(--btn-secondary-shadow);
    color: var(--btn-secondary-color);
}
.secondary:hover{
    background: var(--btn-secondary-bg-hover);
}

.danger{
    color: var(--btn-danger-color);
    background: var(--btn-danger-bg);
    box-shadow: var(--btn-danger-shadow);
}
.danger:hover{
    background: var(--btn-danger-bg-hover);
}

.outline{
    background-color : var(--btn-outline-bg);
    box-shadow: var(--btn-outline-shadow);
    border : 1px solid var(--btn-outline-color);
    color :  var(--btn-outline-color);
}
.outline:hover,
.outline:focus{
    background: var(--btn-outline-bg-hover);
}


#app{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    align-items: center;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    padding-bottom: 10rem;
    max-width: 600px;
    margin: 0 auto;
}
